.text-small {
  font-size: 80%;
}

.text-mutted {
  color: #888;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.float-left {
  float: left !important;
}

.float-right {
  float: right !important;
}

.float-none {
  float: none !important;
}

.gallery-link {
  display: block;
  margin-bottom: 16px;
  overflow: hidden;
  border: 2px solid #aaa;
  max-width: 208px;
}

.gallery-thumb {
  width: 100%;
  height: auto;
  transition: all 1s ease-out;
}

.gallery-link:hover .gallery-thumb {
  border-color: #c83800;
  transform: scale(1.1);
}

.footer {
  max-width: 740px;
}

@media (min-width: 768px) {
  .footer {
    max-width: 740px;
  }
}

.footer .copyrights {
  font-size: 70%;
  color: #888;
}